@font-face {
  font-family: 'NeueMontrealMediumExtraSqueezed';
  src: url('./fonts/OTNeueMontreal-MediumExtraSqueezed.otf') format('opentype');
  font-weight: 500; /* medium */
  font-style: normal;
  font-display: swap;
}
/* App.css */
body, html {
  margin: 0;
  height: 100%;
  font-family: 'OTNeueMontreal-MediumExtraSqueezed', sans-serif;
  background:#f4f1e1; /* Beige Hintergrund */
  scroll-behavior: smooth;
 overflow-x: hidden;
}

.sketch-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none; /* verhindert, dass der Canvas Klicks abfängt */
}

/* Haupt-App Container (Content) */
.App {
  position: relative;
  z-index: 1;  /* über dem Sketch */
  color: black;
  padding: 20px;
  min-height: 100vh;
}


/* Header Styling */
header h1 {
  font-family: 'OTNeueMontreal-MediumExtraSqueezed', sans-serif;
  font-size: 5rem;
  margin: 0;
}

nav ul {
  list-style: none;
  padding: 0;
  margin-top: 1rem;
}

nav ul li {
  display: inline-block;
  margin-right: 20px;
}

nav ul li a {
  font-family: 'Courier New', Courier, monospace;
  font-size: 1rem;
  color: black;
  text-decoration: none;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: rgb(0, 255, 0);
}
/* Mission Page */
.mission-container {
  font-family: 'OTNeueMontreal-MediumExtraSqueezed', sans-serif;
  font-weight: 400;
  font-size: 5rem;
  line-height: 2.2;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: rgb(254, 0, 0);
  margin: -20px; /* gleicht das Padding aus */
  width: calc(100% + 40px); /* 20px links + rechts */

  /* Neu: Zentrierung */
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem; /* Abstand zwischen Text und Bild */
  padding:0;
}


.mission-container h2 {
  font-weight: 400;
  font-size: 4rem;
  line-height: 3.5rem;
  text-transform: uppercase;
  letter-spacing: 0;
  margin: 0;
  max-width: 800px;
}

.mission-image {
  width: 100vw;   /* Volle Breite des Viewports */
  height: 100vh;  /* volle Höhe des Viewports, wenn gewünscht */
  object-fit: cover; /* Damit das Bild nicht verzerrt wird */
  margin: 0;
  padding: 0;
  display: block;  /* Vermeidet zusätzlichen Abstand */
}

.mission-container p {
  font-size: 4%;
  line-height: 3.2;
  max-width: 800px;
}

.mission-container .subtle {
  color: gray;
  font-weight: 300;
}
/* Content */
.content {
  margin-top: 2rem;
  font-size: 3rem;
}
@media (max-width: 768px) {
  .mission-container {
    font-size: 3.5rem;
  }

  .mission-container h2 {
    font-size: 3rem;
  }
}

@media (max-width: 480px) {
  .mission-container {
    font-size: 2.5rem;
  }

  .mission-container h2 {
    font-size: 2rem;
  }
}